.CreateTodoButton{
    background-color: rgb(81, 132, 243);
    box-shadow: 0px 5px 15px rgba(81, 132, 243, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 3;

    /*Animacion*/
    transform: rotate(0);
    transition: .3s ease;
}

.CreateTodoButton:hover{
    transform: rotate(224deg);
}